<script>
    export default {
        data () {
            return {
                __radioStores: new Set(),
            }
        },
        methods: {
            initRadio (data) {
                this.__radioStores.add(data);
            },
            onRadioClick (data, index) {
                const radioStore = this.__radioStores.has(data) ? this.__radioStores.get(data) : null;
                if(radioStore){
                    radioStore.forEach((item, idx) => {
                        item.isActive = false;
                    });
                    radioStore[index].isActive = true;
                }
            }
        }
    }
</script>


