<script>
    export default {
        data () {
            return {
                __optionStores: new Set(),
            }
        },
        methods: {
            initOption (data) {
                this.__optionStores.add(data);
            },
            onOptionClick (data, index) {
                const optionStore = this.__optionStores.has(data) ? this.__optionStores.get(data) : null;
                if(optionStore){
                    optionStore.forEach((item, idx) => {
                        item.isActive = false;
                    });
                    optionStore[index].isActive = true;
                }
            }
        }
    }
</script>


