<template>
    <span class="option" @click="onClick" :class="status">
        <slot name="pre"></slot>
        <span v-if="source[index].content">{{source[index].content}}</span>
        <slot name="post"></slot>
    </span>
</template>
<script>
    import Vue from 'vue';
    export default Vue.extend({
        props: ['source','index'],
        computed: {
            status () {
                if(this.source[this.index].isDisable){
                    return 'disable'
                }else{
                    return this.source[this.index].isActive ? 'active' : ''  
                }
            }
        },
        methods: {
            onClick () {
                this.$emit('onClick', this.index)
            }
        }
    })
</script>


