<template>
    <div>
        </br>
        <p>你最喜欢的蔬菜是：</p>
        </br>
        <radio v-for="(item, index) in data" :source="data" :index="index" @onClick="onRadioClick">
            <i slot="pre">·</i>
        </radio>
        </br>
        </br>
        <p>你的选择是：{{result}}</p>
        </br>
        </br>
    </div>
</template>
<script>
    import Vue from 'vue';
    import {RadioItem, RadioMixin} from '../index.js';
    export default Vue.extend({
        mixins: [RadioMixin],
        components: {
            'radio': RadioItem,
        },
        computed: {
            result () {
                let result = ''
                for(let i = 0; i < this.data.length; i++){
                    if(this.data[i].isActive){
                        result = this.data[i].content
                    }
                }
                return result;
            }
        },
        created () {
            this.initRadio(this.data);
        }
    })
</script>
<style lang="less">
    .radio {
        &.active {
            color: #ef3030;
        }
    }
</style>


