<template>
    <div class="page" :style="style">
        <slot></slot>
    </div>
</template>
<script>
    import Vue from 'vue';
    export default Vue.extend({
        data () {
            return {
                style: {
                    'min-height': screen.availHeight + 'px'
                },
            }
        }
    })
</script>
<style >
    .page {
        width: 100%;
    }
</style>


