<template>
    <div>
        <div @click="$refs.aslider.in()">
            点击我打开侧滑层
        </div>
        <caside ref="aslider" :shy='true'>
            <scroller :direction="'vertical'">
                <div>
                    <div v-for="item in [1,2,3,4,5,6,7,8]">
                        <img src="http://gfs3.gomein.net.cn/T1aCCTBKVv1RCvBVdK_80.jpg">
                    </div>
                    <scroller :direction="'horizontal'">
                        <div v-for="item in [1,2,3,4,5,6,7,8]">
                            <img src="http://gfs3.gomein.net.cn/T1aCCTBKVv1RCvBVdK_80.jpg">
                        </div>
                    </scroller>
                </div>
            </scroller>
        </caside>
    </div>
</template>
<script>
    import Vue from 'vue';
    import {Aside, Scroller} from '../index.js'
    export default Vue.extend({
        components: {
            'caside': Aside,
            'scroller': Scroller
        },
    })
</script>
<style lang="less">
    .aside-container {
        .scroller-container {
            width: 100%;
        }
    }
</style>


