<template>
    <span @click="onClickIcon" class="radio" :class='{"active" : source[index].isActive}'>
        <slot name="pre"></slot>
        <span v-if="source[index].content">{{source[index].content}}</span>
        <slot name="post"></slot>
    </span>
</template>
<script>
    import Vue from 'vue';
    export default Vue.extend({
        props: ['source', 'index'],
        methods: {
            onClick () {
                this.$emit('onClick', this.index);
            },
            onClickIcon () {
                this.$emit('onClick', this.index);
            }
        }
    })
</script>


