<template>
    <div class="option-container">
        <p>你喜爱吃的肉的种类有</p>
        </br>
        <coption v-for="(item, index) in data" :source="data" :index="index" @onClick="onOptionClick">
            <i slot="pre">选项：</i>
            <i slot="post">√</i>
        </coption>
        </br>
        </br>
        <p>你的选择是：{{result}}</p>
        </br>
        </br>

    </div>
</template>
<script>
    import Vue from 'vue';
    import {OptionItem, OptionMixin} from '../index.js';
    export default Vue.extend({
        mixins: [OptionMixin],
        components: {
            'coption': OptionItem,
        },
        computed: {
            result () {
                let result = ''
                for(let i = 0; i < this.data.length; i++){
                    if(this.data[i].isActive){
                        result += this.data[i].content + '; '
                    }
                }
                return result;
            }
        },
        created () {
            this.initOption(this.data);
        }
    })
</script>
<style lang="less" scope>
    .option-container {
        .option {
            margin-right: .1rem;
            display: block;
            &.active {
                color: #ef3030;
            }
            &.disable {
                color: #a3a3a3;
                text-decoration:line-through;
            }
        }
    }
    
</style>


